import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';

interface UserLoggedInResponse {
    status: boolean;
}
@Injectable()
export class CommunicationService {

    constructor(private http: Http) { }

    register(username: String): Observable<string> {
        let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' });
        let options = new RequestOptions({ headers: headers });

        return this.http.post('/register', 'username=' + username, options).map((r: any) => {
            console.log(r);
            return r._body.substring(39);
        }).catch(this.handleError.bind(this));
    }

    handleError(error: Response | any) {
        let errorCode: number;
        if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);

            errorCode = body.errorCode;
        } else {
            errorCode = 0;
        }
        console.error(errorCode);
        return Observable.throw(errorCode);
    }

    checkIfUserActivatedApp(username: string) {
        let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' });
        let options = new RequestOptions({ headers: headers });

        return this.http.get("/user-active?username=" + username, options).map((r: Response) => r.json() as UserLoggedInResponse);
    }

    checkIfUserLoggedIn(username: string) {
        let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded' });
        let options = new RequestOptions({ headers: headers });

        return this.http.get("/user-logged-in?username=" + username, options).map((r: Response) => r.json() as UserLoggedInResponse);
    }
}



