import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { IndexComponent } from './index/index.component';
import { RegisterAppComponent } from './register-app/register-app.component';
import { LoginComponent } from './login/login.component';
import { PasswordChangeComponent } from './password-change/password-change.component';
import { ActivateAccountComponent } from './activate-account/activate-account.component';
import { UserLoggedInComponent } from './user-logged-in/user-logged-in.component';

const routes: Routes = [
  { path: '', redirectTo: '/app', pathMatch: 'full' },
  { path: 'app', component: IndexComponent },
  { path: 'register-app', component: RegisterAppComponent },
  { path: 'login', component: LoginComponent },
  { path: 'user-logged-in', component: UserLoggedInComponent },
  { path: '**', component: IndexComponent }
]
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }


