import { Component, OnInit } from '@angular/core';
import { CommunicationService } from '../communication-service/communication.service';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-register-app',
  templateUrl: './register-app.component.html',
  styleUrls: ['./register-app.component.css']
})
export class RegisterAppComponent implements OnInit {
  private loading: boolean = true;
  private username: string;
  private password: string;
  private userLoggedIn: boolean = false;
  private userActivatedAccount: boolean = false;

  private svgContent: string = "";
  private showAccountActivatedDialog: boolean = false;
  constructor(private communicationService: CommunicationService, private router: Router) { }

  ngOnInit() {
  }

  logUserIn() {
    localStorage.setItem('currentUser', this.username);
    this.loading = true;
    this.communicationService.register(this.username).subscribe(result => {
      document.getElementById("registerQrCode").innerHTML = result;
      this.loading = false;
      this.userLoggedIn = true;
      this.userAccountActivatedCheck();
    });
    //  this.userAccountActivatedCheck();
    //  setTimeout(() => this.loading = false, 2000);
    //  setTimeout(() => this.userActivatedAccount = true, 5000);
  }

  userAccountActivatedCheck() {
    setInterval(() => {
      this.communicationService.checkIfUserActivatedApp(localStorage.getItem('currentUser')).subscribe(userAccountActivated => {
      this.showAccountActivatedDialog = userAccountActivated.status
      this.router.navigate(['/login']); 
    });
    }, 5000);

  }
}



